import React from "react";
import {
  Header,
  Panel,
  PanelHeader,
  Card,
  CardGrid,
  PanelHeaderBack,
  Button,
  MiniInfoCell,
  Div,
  Title,
  Group,
  Footer,
} from "@vkontakte/vkui";
import "./Home.css";

import Icon24MoneyCircle from "@vkontakte/icons/dist/24/money_circle";
import CartLine from "./components/CartLine";

const AboutItem = ({ id, go, activeItem, onIncrementPosition, order }) => {
  return (
    <Panel id={id}>
      <PanelHeader left={<PanelHeaderBack onClick={() => go("category")} />} />
      {activeItem !== null && (
        <React.Fragment>
          {order !== null && <CartLine go={go} order={order} />}
          <Group
            style={{ paddingTop: 40 }}
            header={<Header mode="secondary">Информация о товаре</Header>}
          >
            <img src={activeItem.thumb_photo} />
          </Group>
          <Div>
            <CardGrid>
              <Card size="l">
                <MiniInfoCell textWrap={"full"}>
                  <Title
                    level="1"
                    weight="semibold"
                    style={{ marginBottom: 16 }}
                  >
                    {activeItem.title}
                  </Title>
                </MiniInfoCell>
                <MiniInfoCell
                  after={
                    <Button
                      onClick={() => onIncrementPosition(activeItem)}
                      mode={"outline"}
                    >
                      В корзину
                    </Button>
                  }
                  before={<Icon24MoneyCircle height={32} width={32} />}
                >
                  <Title level="1" weight="semibold">
                    {activeItem.price.number}
                  </Title>
                </MiniInfoCell>
              </Card>
              <Card size="l">
                <Div
                  style={{
                    backgroundColor: "#03825e",
                    borderRadius: 10,
                    color: "antiquewhite",
                  }}
                >
                  {activeItem.description}
                </Div>
              </Card>
            </CardGrid>
          </Div>
        </React.Fragment>
      )}
      <Footer />
    </Panel>
  );
};

export default AboutItem;
